.topbarContainer{
    height: 50px;
    width: 100%;
    background-color: aquamarine ;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 999;
}

.topbarLeft{
    flex: 3;
}

.logo{
    font-size: 24px;
    margin-left: 20px;
    font-weight: bold;
    color: blue;
    cursor: pointer ;
}

.searchIcon{
    font-size: 20px !important ;
    margin-left: 10px;
    color: blue !important;
}

.searchInput{
    border: none;
     width: 70%;
}

.searchInput:focus{
    outline: none;
}

.topbarCenter{
    flex: 5;
}

.searchbar{
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 30px;
    display: flex;
    align-items: center;
}

.topbarRight{
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.topbarLink{
    margin-right: 30px;
    font-size: 25px !important;
    cursor: pointer;
    font-weight: bold !important;
    color: blue;
}

.topbarIcons{
    display: flex;
}

.topbarIconItem{
    margin-right: 15px;
    cursor: pointer;
    position: relative;
    color: blue;
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}

.topbarImg{
     width: 35px;
     height: 35px;
     border-radius: 50%;
     object-fit: cover;
     cursor: pointer;
     border: 1px solid blue;
}